@import url(https://fonts.googleapis.com/css?family=Open+Sans);
*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
}

header {
  width: 100%;
  height: 80px;
  padding: 20px;
  margin-bottom: 25px;
  background-color: rgb(80, 255, 203);
}

.input-container {
  width: 100%;
  height: 100px;
  padding: 20px 40px;
  display: flex;
  margin: auto;
  justify-content: center;
  padding-left: 0px;
}

.input-container h4 {
  padding-top: 5px;
}

.input-container input {
  width: 70%;
  height: 35px;
  padding: 5px;
  color: gray;
  margin-left: 20px;
  letter-spacing: 3px;
  font-weight: 600;
}

.input-container input:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgb(80, 255, 203);
  border: 1px solid rgb(0, 183, 255);
}

.header-div {
  width: 30%;
  text-align: right;
}

.input-form-div {
  width: 70%;
  text-align: left;
}

@media only screen and (max-width: 560px) {
  .input-container {
    padding-right: 0px;
    flex-wrap: wrap;
  }

  .input-container > div {
    width: 100%;
    text-align: center;
  }

  .input-container .header-div {
    text-align: left;
    padding-left: 5vw;
  }

  .input-container input {
    width: 90%;
    margin: 0px;
  }
}

.container, .backdrop, textarea {
  width: 80%;
  height: 400px;
}

.highlights, textarea {
  padding: 10px;
  font: 20px/28px 'Open Sans', sans-serif;
  font-size: 20px;
  letter-spacing: 1.5px;
}

.container {
  display: block;
  margin: 0 auto;
}

.backdrop {
  position: absolute;
  z-index: 1;
  background-color: #fff;
  overflow: auto;
}

.highlights {
    white-space: pre-wrap;
    word-wrap: break-word;
    text-align: left;
    color: transparent;
}

textarea {
  display: block;
  position: absolute;
  z-index: 2;
  margin: 0;
  border-radius: 0;
  color: #444;
  background-color: transparent;
  overflow: auto;
  resize: none;
}

mark {
  border-radius: 3px;
  color: transparent;
}

.even {
  background-color: #ffae00;
}

._odd {
  background-color: #ffdc89;
}

.container .header {
  display: flex;
}

.container .header h4 {
  width: 20%;
  text-align: left;
}

.container .header p {
  width: 80%;
  text-align: right;
  color: rgb(253, 136, 57);
  font-weight: 600;
  padding: 3px;
}

textarea:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgb(80, 255, 203);
  border: 1px solid rgb(0, 183, 255);
}

@media only screen and (max-width: 560px) {
  .container, .backdrop, textarea {
    width: 92%;
  }

  .container h4 {
    text-align: left;
  }
}

footer {
  margin-top: 50px;
  width: 100%;
  height: 70px;
  background-color: rgb(80, 255, 203);
  padding: 20px;
}

.App {
  text-align: center;
}

.content {
  padding: 20px;
  padding-left: 0px;
}

h4 {
  font-size: 20px;
}

@media only screen and (max-width: 560px) {
  .content {
    padding-right: 0px;
    padding-top: 0px;
  }
}

